<template>
  <div :class="{'drawer-opened': openDrawer}">
    <MainMenu title="" />
    <div class="toolbox-order-list">
      <div class="search-box">
        <mu-text-field hintText="" title="" class="search-input" v-model="searchString" ref="searchField" />
        <mu-icon-button icon="search" class="search-btn" color="#FFF" @click="handleSearch()"/>
      </div>
      <div class="top-btn">
        <mu-raised-button label="" icon="print" class="raised-button" @click="exportData" secondary/>
      </div>
    </div>
    <div id="filterBox" class="filterbox-order-list">
      <div>
      <el-select v-model="timeType" @change="filterTimeType" placeholder="" class="filter-timetype">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="2"></el-option>
        <el-option label="" value="3"></el-option>
      </el-select>
      <el-date-picker
        v-model="timeRange"
        @change="filterTimeRange"
        type="daterange"
        placeholder=""
        class="filter-timerange">
      </el-date-picker>
      </div>
      <el-select v-model="productId" multiple @change="filterProductType" placeholder="" class="filter-box-div">
        <el-option
          v-for="item in allProductType"
          :key="item.productId"
          :label="item.productName"
          :value="item.productId">
        </el-option>
      </el-select>
      <el-select v-model="orderStatus" multiple @change="filterOrderType" placeholder="" class="filter-box-div">
        <el-option
          v-for="item in orderStatusOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <el-table :data="orderList.content" border fit highlight-current-row @current-change="handleCurrentChange" :height="tableHeight">
      <el-table-column type="index" fixed="left" width="62">
      </el-table-column>
      <el-table-column prop="orderId" fixed="left" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="productName" label="" min-width="130">
      </el-table-column>
      <el-table-column prop="userPhone" label="" min-width="130">
      </el-table-column>
      <el-table-column prop="cityCode" :formatter="cityName" label="" min-width="90">
      </el-table-column>
      <el-table-column prop="location" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="carInfo" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="orderTime" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="appointTime" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="workerName" label="" min-width="130">
      </el-table-column>
      <el-table-column prop="phone" label="" min-width="130">
      </el-table-column>
      <el-table-column prop="orderStatus" label="" :formatter="orderStatusName" min-width="150">
      </el-table-column>
      <el-table-column prop="payStatus" label="" :formatter="payStatusName" min-width="150">
      </el-table-column>
      <el-table-column prop="payID" label="ID" min-width="150">
      </el-table-column>
      <el-table-column prop="payTime" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="saleAmount" label="" :formatter="moneyFormatter" min-width="90">
      </el-table-column>
      <el-table-column prop="deductionAmount" label="" :formatter="moneyFormatter" min-width="90">
      </el-table-column>
      <el-table-column prop="payAmount" label="" :formatter="moneyFormatter" min-width="120">
      </el-table-column>
      <el-table-column prop="realPayAmount" label="" :formatter="moneyFormatter" min-width="120">
      </el-table-column>
      <el-table-column prop="payName" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="memo" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="remark" label="" min-width="150">
      </el-table-column>
      <el-table-column fixed="right" prop="date" label="" min-width="100">
        <template scope="scope">
          <el-button v-if="(scope.row.orderStatus < 50) && (selectedId === scope.row.orderId)" @click="openDialog" type="danger" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div id="tableFooter">
      <el-pagination
        @size-change="handlePageSizeChange"
        @current-change="handlePageNumberChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="orderList.totalElements">
      </el-pagination>
    </div>
    <mu-dialog :open="dialog" title="" @close="closeDialog">
      <span class="dialog-help-text"></span>
      <mu-flat-button slot="actions" @click="closeDialog" primary label="" />
      <mu-flat-button slot="actions" primary @click="doDeleteOrder" label="" />
    </mu-dialog>
    <mu-snackbar v-if="snackbar" :message="snackbarMsg" action="" @actionClick="hideSnackbar" @close="hideSnackbar" />
  </div>
</template>
<script>
import Vue from 'vue'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import moment from 'moment'
import MainMenu from '../units/mainMenu'
import { Button, Select, Option, Table, TableColumn, Input, Tag, DatePicker, Pagination } from 'element-ui'
import snackbar from 'muse-components/snackbar'
import flatButton from 'muse-components/flatButton'
import dialog from 'muse-components/dialog'
import raisedButton from 'muse-components/raisedButton'

Vue.use(Button)
Vue.use(Select)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Option)
Vue.use(Input)
Vue.use(Tag)
Vue.use(DatePicker)
Vue.use(Pagination)
Vue.component(raisedButton.name, raisedButton)
Vue.component(flatButton.name, flatButton)
Vue.component(dialog.name, dialog)
Vue.component(snackbar.name, snackbar)
export default {
  name: 'ProductList',
  components: {
    MainMenu
  },
  data () {
    return {
      funcTableHeight: {},
      selectedId: '',
      dialog: false,
      tableHeight: 300,
      orderStatusOptions: [{
        value: '10',
        label: '/'
      }, {
        value: '20',
        label: '/'
      }, {
        value: '30',
        label: ''
      }, {
        value: '40',
        label: '/'
      }, {
        value: '50',
        label: '/'
      }, {
        value: '60',
        label: ''
      }, {
        value: '90',
        label: ''
      }, {
        value: '91',
        label: ''
      }],
      orderStatus: [],
      payCode: [],
      productId: [],
      timeType: '1',
      timeRange: [
        moment(),
        moment()
      ],
      searchString: '',
      pageSize: 20,
      currentPage: 1
    }
  },
  mounted () {
    this.adjustTableHeight()
    this.getAllProductType()
    this.getData()
  },
  destroyed () {
    window.removeEventListener('resize', this.funcTableHeight)
  },
  computed: {
    ...mapState([
      'cities',
      'city',
      'group',
      'snackbar',
      'snackbarMsg',
      'openDrawer',
      'allProductType',
      'orderList'
    ]),
    ...mapGetters([
      'cityAndGroup'
    ])
  },
  watch: {
    cityAndGroup: function () {
      this.getData()
    }
  },
  methods: {
    ...mapMutations([
      'showSnackbar',
      'hideSnackbar'
    ]),
    ...mapActions([
      'getAllProductType',
      'getOrdersByCondition',
      'getOrdersByKeyword',
      'cancelOrder',
      'exportByCondition',
      'exportByKeyword'
    ]),
    adjustTableHeight () {
      // 
      const z = this
      function debounce (func, wait) {
        let timer = null
        return function () {
          clearTimeout(timer)
          timer = setTimeout(func, wait)
        }
      }
      function setTableHeight () {
        const filterBoxHeight = document.getElementById('filterBox').offsetHeight || 46
        z.tableHeight = window.innerHeight - 74 - filterBoxHeight - 36
      }
      setTableHeight()
      z.funcTableHeight = debounce(setTableHeight, 400)
      window.addEventListener('resize', this.funcTableHeight)
    },
    cityName (row, column, cityCode) {
      let cityName = cityCode
      this.cities.map(c => {
        if (c.cityCode === cityCode) {
          cityName = c.cityName
        }
      })
      return cityName
    },
    moneyFormatter (row, column, money) {
      return (money / 100).toFixed(2)
    },
    orderStatusName (row, column, orderStatus) {
      let name = orderStatus
      this.orderStatusOptions.map(function (o) {
        if (o.value === orderStatus) {
          name = o.label
        }
      })
      return name
    },
    payStatusName (row, column, payStatus) { // 01
      return payStatus === '1' ? '' : ''
    },
    getData () {
      if (this.searchString) {
        let searchData = {
          keyword: this.searchString.trim(),
          cityCode: this.city,
          leaderId: this.group,
          page: this.currentPage - 1,
          size: this.pageSize
        }
        this.getOrdersByKeyword(searchData)
      } else {
        let data = {
          cityCode: this.city,
          leaderId: this.group,
          payCode: this.payCode.toString(),
          productId: this.productId.toString(),
          orderStatus: this.orderStatus.toString(),
          timeType: this.timeType,
          startTime: moment(this.timeRange[0]).format('YYYY-MM-DD'),
          endTime: moment(this.timeRange[1]).format('YYYY-MM-DD'),
          page: this.currentPage - 1,
          size: this.pageSize
        }
        this.getOrdersByCondition(data)
      }
    },
    openDialog () {
      this.dialog = true
    },
    closeDialog () {
      this.dialog = false
    },
    filterTimeType () {
      this.searchString = ''
      this.getData()
    },
    filterTimeRange () {
      this.searchString = ''
      this.getData()
    },
    filterProductType () {
      this.searchString = ''
      this.getData()
      this.funcTableHeight()
    },
    filterOrderType () {
      this.searchString = ''
      this.getData()
      this.funcTableHeight()
    },
    handleCurrentChange (row, oldRow) {
      this.selectedId = row && row.orderId
    },
    doDeleteOrder () {
      const z = this
      z.cancelOrder({
        orderId: this.selectedId
      }).then(function () {
        z.getData()
      })
      z.closeDialog()
    },
    handleSearch () {
      this.getData()
    },
    handlePageSizeChange (size) {
      this.pageSize = size
      this.currentPage = 1
      this.getData()
    },
    handlePageNumberChange (currentPage) {
      this.currentPage = currentPage
      this.getData()
    },
    exportData () {
      if (this.searchString) {
        let searchData = {
          keyword: this.searchString.trim(),
          cityCode: this.city,
          leaderId: this.group
        }
        this.exportByKeyword(searchData)
      } else {
        let data = {
          cityCode: this.city,
          leaderId: this.group,
          payCode: this.payCode.toString(),
          productId: this.productId.toString(),
          orderStatus: this.orderStatus.toString(),
          timeType: this.timeType,
          startTime: moment(this.timeRange[0]).format('YYYY-MM-DD'),
          endTime: moment(this.timeRange[1]).format('YYYY-MM-DD')
        }
        this.exportByCondition(data)
      }
      console.log('export')
    }
  }
}
</script>

<style>
.toolbox-order-list .mu-text-field-input {
  color: #fff;
}
.filterbox-order-list .filter-timetype .el-input__inner {
  border-radius: 4px 0 0 4px;
  border-right: 0;
}
.filterbox-order-list .filter-timerange .el-input__inner {
  border-radius: 0 4px 4px 0;
}
</style>
<style scoped>
.toolbox-order-list {
  position: absolute;
  top: 0;
  right: 30px;
  color: #fff;
  height: 74px;
  line-height: 74px;
}
.toolbox-order-list .search-box {
  position: relative;
  display: inline-block;
}
.toolbox-order-list .search-box .search-input {
  width: 180px;
  margin-top: -2px;
}
.toolbox-order-list .search-box .search-btn {
  position: absolute;
  bottom: 16px;
  right: -8px;
  width: 36px;
  height: 36px;
  padding: 0;
}
.toolbox-order-list .top-btn {
  float: right;
  margin-left: 30px;
  margin-top: 8px;
}
#filterBox {
  display: flex;
  flex-wrap: nowrap;
}
#filterBox .filter-box-div {
  margin: 5px;
  flex: 1;
}
#filterBox .filter-timetype {
  width: 8em;
  margin: 5px -5px 5px 5px;
}
#filterBox .filter-timerange {
  width: 16em;
  margin: 5px 5px 5px 0;
}
#tableFooter {
  text-align: right;
  padding: 2px 5px;
}
</style>



